.profile-personal {

	&__item {
		display: grid;
		gap: 2rem;

		&:not(:last-child) {
			padding-bottom: 4rem;
			border-bottom: 0.1rem solid var(--line-color);

			@media(max-width: 767px) {
				padding-bottom: 3rem;
			}
		}

		&:not(:first-child) {
			padding-top: 4rem;

			@media(max-width: 767px) {
				padding-top: 3rem;
			}
		}
	}

	&__item-title {
		@extend %text-16;
		display: block;
	}

	&__row {
		display: grid;
		align-items: center;
		gap: 9rem;

		@media(max-width: 1024px) {
			gap: 3rem;
		}

		@media(max-width: 767px) {
			gap: 1.5rem;
		}

		@media(min-width: 768px) {
			grid-template-columns: 70% 1fr;
		}

		@media(min-width: 1025px) {
			grid-template-columns: 26.4rem * 2+3rem 1fr;
		}
	}

	&__fields {
		display: grid;
		gap: 2rem 3rem;

		@media(min-width: 768px) {
			grid-template-columns: repeat(2, 1fr);
		}
	}

	// .profile-personal__gender-radios

	&__row-field {
		display: grid;
		grid-template-columns: 1fr 19.5rem;
		align-items: center;

		&:not(:last-child) {
			@media (min-width: 501px) {
				margin-bottom: 2.2rem;
			}
		}
	}

	&__gender-radios {
		display: flex;
	}

	// .profile-personal__gender-radio

	&__gender-radio {
		width: 50%;
		cursor: pointer;

		.radio-style {
			border: 0.1rem solid #d0d0d0;
			height: 4.6rem;
			@extend %flex-center;
		}

		.radio-text {
			@extend %text-13;
			color: #000;
		}

		.radio-box:checked+.radio-style {
			border-color: var(--dark);
			background-color: var(--dark);

			.radio-text {
				color: #fff;
			}
		}
	}

	// .profile-personal__change-password-btn

	&__change-password-btn {
		@extend %flex-center;
		@extend %text-13;
		color: #000;
		padding: 1.35rem;
		border: 0.1rem solid #d0d0d0;

		@media (any-hover: hover) {
			&:hover {
				border-color: var(--green-second);
				color: var(--green-second);
			}
		}
	}
}

.notify {
	@extend %vertical-align;

	&__icon {
		width: 2.4rem;
		height: 2.4rem;
		flex-shrink: 0;
		margin-right: 0.8rem;
	}
}

.profile-notify {
	@extend %vertical-align;
	padding: 1.6rem;

	&.success {
		background-color: rgba(#479458, 0.1);
	}

	&.error {
		background-color: rgba(#ee3333, 0.1);
	}
}
